/**
 * PetCareApp - Serwis wizyt
 * Obs≈Çuga rezerwacji i zarzƒÖdzania wizytami
 * @author VS
 */

import { appointmentApi } from './api';

/**
 * Serwis wizyt - zarzƒÖdzanie wizytami w klinice
 */
export const appointmentService = {
    /**
     * Pobranie listy wizyt u≈ºytkownika
     * @param {Object} filters - Filtry (status, data, etc.)
     * @returns {Promise<Array>} Lista wizyt
     */
    async getAppointments(filters = {}) {
        const response = await appointmentApi.get('/appointments', { params: filters });
        return response.data;
    },

    /**
     * Pobranie szczeg√≥≈Ç√≥w wizyty
     * @param {string} appointmentId - ID wizyty
     * @returns {Promise<Object>} Dane wizyty
     */
    async getAppointmentById(appointmentId) {
        const response = await appointmentApi.get(`/appointments/${appointmentId}`);
        return response.data;
    },

    /**
     * Utworzenie nowej wizyty
     * @param {Object} appointmentData - Dane wizyty
     * @returns {Promise<Object>} Utworzona wizyta
     */
    async createAppointment(appointmentData) {
        const response = await appointmentApi.post('/appointments', appointmentData);
        return response.data;
    },

    /**
     * Aktualizacja wizyty
     * @param {string} appointmentId - ID wizyty
     * @param {Object} updates - Dane do aktualizacji
     * @returns {Promise<Object>} Zaktualizowana wizyta
     */
    async updateAppointment(appointmentId, updates) {
        const response = await appointmentApi.put(`/appointments/${appointmentId}`, updates);
        return response.data;
    },

    /**
     * Anulowanie wizyty
     * @param {string} appointmentId - ID wizyty
     * @param {string} reason - Pow√≥d anulowania
     * @returns {Promise<void>}
     */
    async cancelAppointment(appointmentId, reason) {
        await appointmentApi.post(`/appointments/${appointmentId}/cancel`, { reason });
    },

    /**
     * Potwierdzenie wizyty
     * @param {string} appointmentId - ID wizyty
     * @returns {Promise<Object>} Potwierdzona wizyta
     */
    async confirmAppointment(appointmentId) {
        const response = await appointmentApi.post(`/appointments/${appointmentId}/confirm`);
        return response.data;
    },

    /**
     * Pobranie dostƒôpnych termin√≥w
     * @param {string} vetId - ID weterynarza
     * @param {string} date - Data (YYYY-MM-DD)
     * @param {string} serviceType - Typ us≈Çugi
     * @returns {Promise<Array>} Dostƒôpne terminy
     */
    async getAvailableSlots(vetId, date, serviceType) {
        const response = await appointmentApi.get('/appointments/slots', {
            params: { vetId, date, serviceType }
        });
        return response.data;
    },

    /**
     * Pobranie listy weterynarzy
     * @param {string} specialization - Specjalizacja (opcjonalnie)
     * @returns {Promise<Array>} Lista weterynarzy
     */
    async getVeterinarians(specialization = null) {
        const params = specialization ? { specialization } : {};
        const response = await appointmentApi.get('/veterinarians', { params });
        return response.data;
    },

    /**
     * Pobranie typ√≥w wizyt/us≈Çug
     * @returns {Promise<Array>} Lista typ√≥w wizyt
     */
    async getAppointmentTypes() {
        const response = await appointmentApi.get('/appointment-types');
        return response.data;
    },

    /**
     * Pobranie wizyt na dany dzie≈Ñ (dla weterynarza)
     * @param {string} date - Data (YYYY-MM-DD)
     * @returns {Promise<Array>} Lista wizyt
     */
    async getDaySchedule(date) {
        const response = await appointmentApi.get('/appointments/schedule', {
            params: { date }
        });
        return response.data;
    },

    /**
     * Pobranie harmonogramu weterynarza
     * @param {string} vetId - ID weterynarza
     * @param {string} startDate - Data poczƒÖtkowa
     * @param {string} endDate - Data ko≈Ñcowa
     * @returns {Promise<Object>} Harmonogram
     */
    async getVetSchedule(vetId, startDate, endDate) {
        const response = await appointmentApi.get(`/veterinarians/${vetId}/schedule`, {
            params: { startDate, endDate }
        });
        return response.data;
    },

    /**
     * Ustawienie dostƒôpno≈õci weterynarza
     * @param {Object} availabilityData - Dane dostƒôpno≈õci
     * @returns {Promise<Object>} Zaktualizowana dostƒôpno≈õƒá
     */
    async setAvailability(availabilityData) {
        const response = await appointmentApi.post('/veterinarians/availability', availabilityData);
        return response.data;
    },

    /**
     * Oznaczenie wizyty jako zako≈Ñczonej
     * @param {string} appointmentId - ID wizyty
     * @param {Object} summary - Podsumowanie wizyty
     * @returns {Promise<Object>} Zako≈Ñczona wizyta
     */
    async completeAppointment(appointmentId, summary) {
        const response = await appointmentApi.post(`/appointments/${appointmentId}/complete`, summary);
        return response.data;
    },

    /**
     * Pobranie statystyk wizyt (dla admina)
     * @param {Object} filters - Filtry czasowe
     * @returns {Promise<Object>} Statystyki
     */
    async getAppointmentStats(filters = {}) {
        const response = await appointmentApi.get('/appointments/stats', { params: filters });
        return response.data;
    }
};

export default appointmentService;
